import React from 'react';
import {
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCardFooter,
    CTextarea,
  CButton,
  CForm,
  CFormGroup,
  CSelect,
  CInput,
  CLabel} from '@coreui/react';

import CIcon from '@coreui/icons-react';

export default class NewStation extends React.Component {
    render() {
        return (
            <CForm action="" method="post">
          <CCard>
            <CCardHeader>
              Tambah
              <small> Stasiun</small>
            </CCardHeader>
            <CCardBody>
                <CFormGroup row className="my-0">
                <CCol xs="8">
                  <CFormGroup>
                    <CLabel htmlFor="city">Nama</CLabel>
                    <CInput placeholder="Nama Stasiun" />
                  </CFormGroup>
                </CCol>
                <CCol xs="4">
                  <CFormGroup>
                    <CLabel htmlFor="postal-code">Kode</CLabel>
                    <CInput placeholder="Kode" />
                  </CFormGroup>
                </CCol>
              </CFormGroup>
                <CFormGroup>
                  <CLabel htmlFor="nf-password">Kepala Stasiun</CLabel>
                    <CSelect custom name="select" required>
                      <option value="0">Silahkan pilih</option>
                      <option value="h1">Kepala Satu</option>
                      <option value="h2">Kepala Dua</option>
                      <option value="h3">Kepala 3</option>
                    </CSelect>
                </CFormGroup>
                <CFormGroup row className="my-0">
                <CCol xs="6">
                  <CFormGroup>
                    <CLabel htmlFor="lat">Latitude</CLabel>
                    <CInput placeholder="Ketik disini..." />
                  </CFormGroup>
                </CCol>
                <CCol xs="6">
                  <CFormGroup>
                    <CLabel htmlFor="long">Longitude</CLabel>
                    <CInput placeholder="Ketik disini..." />
                  </CFormGroup>
                </CCol>
              </CFormGroup>
              <CFormGroup>
                  <CLabel>Alamat</CLabel>
                  <CTextarea 
                      name="address" 
                      rows="3"
                      placeholder="Tulis disini..." 
                    />
              </CFormGroup>
            </CCardBody>
            <CCardFooter>
              <CButton type="submit" size="sm" color="primary"><CIcon name="cil-scrubber" /> Simpan</CButton> <CButton type="reset" size="sm" color="danger"><CIcon name="cil-ban" /> Reset</CButton>
            </CCardFooter>
          </CCard>
              </CForm>
        );
    }
}