import React from 'react';
import NewStation from './NewStation';

import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CProgress,
  CRow} from '@coreui/react';
import CIcon from '@coreui/icons-react';

const dummyData = [
    {
        name: "Stasiun Bandung",
        code: "BD",
        head: "h1",
        latitude: -7.0273,
        longitude: 107.5201,
        address: "Jalan Stasiun Timur 1 – Kota Bandung"
    },
    {
        name: "Stasiun Andir",
        code: "AND",
        head: "h2",
        latitude: -7.0273,
        longitude: 107.5201,
        address: "Jalan Ciroyom No.1, Ciroyom, Andir – Kota Bandung"
    },
    {
        name: "Stasiun Kiaracondong",
        code: "KAC",
        head: "h2",
        latitude: -7.0273,
        longitude: 107.5201,
        address: "Jalan Babakan Sari Kiaracondong, Kiaracondong – Kota Bandung"
    }
];

export default class ListStation extends React.Component { 
  constructor(props) {
    super(props);
    this.loading = props.loading;
  }

  componentDidMount() {
    this.loading(false);
  }

  render() {

    return (
        <CRow>
            <CCol md="8">
                <CCard>
                    <CCardHeader>
                        Daftar Stasiun
                    </CCardHeader>
                    <CCardBody>
                    <table className="table table-hover table-outline mb-0 d-none d-sm-table">
                        <thead className="thead-light">
                        <tr>
                            <th className="text-center"><CIcon name="cil-user" /></th>
                            <th>Nama Stasiun</th>
                            <th>Statistik Pengunjung</th>
                        </tr>
                        </thead>
                        <tbody>
                        {
                            dummyData.map((item) => {
                                return (<tr>
                            <td className="text-center">
                            <div className="c-avatar">
                                <img src={'/avatars/1.jpg'} className="c-avatar-img" alt="admin@bootstrapmaster.com" />
                            </div>
                            </td>
                            <td>
                            <div>{item.name}</div>
                            <div className="small text-muted">
                                <span>{item.code}</span> | {item.address}
                            </div>
                            </td>
                            <td>
                            <div className="clearfix">
                                <div className="float-left">
                                <strong>50%</strong>
                                </div>
                                <div className="float-right">
                                <small className="text-muted">Jun 11, 2020 - Jul 10, 2020</small>
                                </div>
                            </div>
                            <CProgress className="progress-xs" color="success" value="50" />
                            </td>
                        </tr>)
                            })
                        }
                        </tbody>
                    </table>
                    </CCardBody>
                </CCard>
            </CCol>
        
        <CCol md="4">      
              <NewStation />
            </CCol>
        </CRow>
    );
  }
}
